import React, { useEffect, useState } from "react";
import logo from "./logo.svg";
import "./App.css";

const sampleApi = "https://reqres.in/api/users";

async function addCache(urls) {
  const myCache = await window.caches.open("response-cache");
  await myCache.addAll(urls);
  console.log("added cache", myCache);
}

function App({ broadcast }) {
  const [users, setUsers] = useState([]);
  const userData = new FormData();

  useEffect(() => {
    fetch(sampleApi)
      .then(res => res.json())
      .then(res => setUsers(res))
  }, []);

  function handleInputChange(event) {
    const target = event.target;
    const name = target.name;
    const value = (target.type = "text" ? target.value : target.files[0]);
    userData.append([name], value)
  }

  function onSubmit() {
  
   fetch(sampleApi, {
      method: "POST",
      body: userData,
    })
      .then(res => res.json())
      .then(res => console.log("success",res))
      .catch(err => console.error("failed",err));
  }
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          {users && JSON.stringify(users)}
        </p>
        <div>
          <label>upload image </label>
          <input
            type="file"
            name="image"
            onChange={handleInputChange}
          />
          <br />
          <label>name </label>
          <input
            type="text"
            name="name"
            onChange={handleInputChange}
          />
          <br />
          <button onClick={onSubmit}>submit</button>
        </div>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        <button onClick={() => addCache([sampleApi])}>Register Router</button>
      </header>
    </div>
  );
}

export default App;
